body {
    background-color: #f5f6fa;
    min-height: 100%;
}

/* sidebar menu */
.sidebar-menu.ui.inverted.menu {
    background-color: #40739e;
    overflow-y: auto;
}

.sidebar-menu.ui.inverted.menu a.item:hover {
    background: rgba(255, 255, 255, .78);
    color: #00a8ff;
}

.sidebar-menu.ui.vertical.menu .item > i.icon {
    float: left;
    margin: 0 .5em 0 0;
}

.sidebar-menu.ui.vertical.menu .item {
    height: 5em;
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 16px;
}

.sidebar-menu.ui.vertical.menu .header.item {
    font-size: 1.5em;
    justify-content: center;
}

.sidebar-menu.ui.vertical.menu .header.item .ui.header {
    color: #fff;
}


/* top menu */
.top-menu {
    padding: 12px 0;
    background-color: #fff !important;
}

.top-menu .ui.input > input {
    border-radius: 1.2rem;
}

.top-menu-small .ui.vertical.menu {
    width: 100%;
}

.top-menu-small .ui.vertical.menu .item ,
.top-menu-small .ui.vertical.menu .menu.right {
    display: inline-block;
}

.sidebar-trigger {
    float: left !important;
}

/* media query */

@media (min-width: 992px) {
    #page-content {
        width: calc(100% - 260px);
    }

}
@media (max-width: 992px) {
    .top-menu {
        margin-bottom: 20px;
    }
}
.dashboard-hero {
    width: 100% !important;
    margin: 10px !important;
}

.dashboard-hero-title {
    font-size: 3em !important;
    text-transform: uppercase !important;
    color: #40739e !important;
}

.dashboard-hero-text {
    line-height: 2em !important;
    font-weight: 300 !important;
    color: #616161 !important;
    margin-top: 0 !important;
}

.quote-text {
    color: #afa4a5;
}
.dashboard-hero-text footer.quote-text,
.dashboard-hero-text q.quote-text {
    font-style: italic;
}



.dashboard-hero-text footer {
    display: inline-block;
    padding-left: 10px;
}

.dashboard-hero-text footer:before {
    content: '- ';
}

.ui.top.attached.label:first-child+:not(.attached) {
    margin-top: 0!important;
}

.task-container {
    margin: 20px 5px;
}
